.Navbar {
    background: rgb(0, 0, 0);
    -webkit-box-shadow: 2px 0px 5px #858585;
            box-shadow: 2px 0px 5px #858585;
    width: 80px;
    height: 100vh;
    top: 0;
    position: fixed;
    z-index: 9999;
}
#navIcons {
    color: rgb(255, 255, 255);
    font-size: 25px;
    padding: 10px;
    margin: 10px 0px;
    border: 2px solid rgb(51, 51, 51);
    border-radius: 50%;
    background: -webkit-gradient(linear, left top, right top, from(rgb(107, 107, 107)) , to(rgb(17, 4, 4)));
    background: -webkit-linear-gradient(left, rgb(107, 107, 107) , rgb(17, 4, 4));
    background: -o-linear-gradient(left, rgb(107, 107, 107) , rgb(17, 4, 4));
    background: linear-gradient(to right, rgb(107, 107, 107) , rgb(17, 4, 4));
}
#navIcons:hover {
    background: black;
    color: rgb(255, 255, 255);
    border: 2px solid rgb(51, 51, 51);
}
#firstIcon {
    margin-top: 100px;
}


