.HomeNavbar {
    background: -webkit-gradient(linear , left top, left bottom , from(rgba(49, 49, 49, 0.5)) , to(rgba(0, 0, 0, 0.8)));
    background: -webkit-linear-gradient(rgba(49, 49, 49, 0.5) , rgba(0, 0, 0, 0.8));
    background: -o-linear-gradient(rgba(49, 49, 49, 0.5) , rgba(0, 0, 0, 0.8));
    background: linear-gradient(rgba(49, 49, 49, 0.5) , rgba(0, 0, 0, 0.8));
    width: 100%;
    height: 22vh;
    padding: 10px 20px;
    bottom: 0;
    position: fixed;
    text-align: center;
}
li {
    display: inline-block;
    margin: 0px 18px;
}
#homeIcons {
    color: rgb(211, 211, 211);
    font-size: 15px;
    margin: 40px 0px;
    padding: 10px 20px;
}
#homeIcons:hover {
    border-bottom: 2px solid rgb(255, 255, 255);
    border-bottom-right-radius: 50%;
}


