/* body */
.ShopBody {
    background-color: black;
}
/* Tab Navbar */
.row {
    width: 94%;
    margin-left: 6%;
    position: fixed;
}
/* Inside Tab */
.griarteOm{
    width: 350px;
    float: left;
    margin: 0px 3px;
}
.card-content {
    background: black;
}
#cardTitle {
    color:rgb(145, 145, 145);
    font-size: 22px;
    padding:10px;
    margin: 0;
}
#shirtPrice{
    color: #26a69a;
}
#shirtQuickView {
    color: rgb(68, 68, 68);
    font-size: 15px;
    border: 2px solid black;
    padding-left: 30px;
    margin: 0;
}
/* add to cart */
#addToCart {
    background: -webkit-gradient(linear, left top, right top, from(rgb(169, 169, 169)) , to(rgb(169, 169, 169)));
    background: -webkit-linear-gradient(left, rgb(169, 169, 169) , rgb(169, 169, 169));
    background: -o-linear-gradient(left, rgb(169, 169, 169) , rgb(169, 169, 169));
    background: linear-gradient(to right, rgb(169, 169, 169) , rgb(169, 169, 169));
    border-radius: 20%;
    border: none;
}
#pickSize {
    background-color: black;
}
#shareThis {
    width: 25px;
    height: 25px;
    margin: 20px 5px 0px 5px;
}


